import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Navigation from "./Navigation";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import profileImg from "../images/Avatar-1.jpg";

import * as styles from "./Profile.module.css";

import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import WorkIcon from "@material-ui/icons/Work";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
  section: {
    width: "100%",
    overflow: "hidden",
    position: "relative",
    zIndex: "1",
  },
}));

export default function Profile() {
  const classes = useStyles();

  return (
    <Container style={{ marginTop: -120, zIndex: 2, position: "relative" }}>
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <img
            src={profileImg}
            style={{
              width: "80%",
              maxWidth: 400,
              margin: "auto",
              zIndex: 999,
              borderRadius: "4px",
            }}
            alt="Oliver Tembo"
          />
        </Grid>
        <Grid item xs={8}>
          <Navigation />

          <Paper className={classes.paper} style={{ marginTop: "20px" }}>
            <Typography variant="h3" style={{ textAlign: "center" }}>
              Oliver Tembo
            </Typography>

            <Typography variant="h5" style={{ textAlign: "center" }}>
              Full Stack Engineer
            </Typography>
            <Typography variant="p" style={{ textAlign: "center" }}>
              Web, Augmented Reality, Virual Reality
            </Typography>
            <ul className={styles.links}>
              <li>
                <a target="_blank" href="https://github.com/olivertembo">
                  <GitHubIcon />
                </a>
              </li>
              <li>
                <a
                  target="_blank"
                  href="https://www.linkedin.com/in/oliver-tembo-26738812b/"
                >
                  <LinkedInIcon />
                </a>
              </li>
              <li>
                <a
                  target="_blank"
                  href="https://www.upwork.com/freelancers/olivertembo2"
                >
                  <WorkIcon />
                </a>
              </li>
            </ul>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
}
